import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		uniSwipeAction,
		uniSwipeActionItem
	},
	data() {
		return {
			title: 'map',
			latitude: 30.6565202250,	//默认天府广场, 如果获取当前位置成功则替换为当前位置
			longitude: 104.0659332275,
			scale: 10,
			showLocation: true,
			covers: [],
			merchantList: []
		}
	},
	onLoad(option) {
		let that = this;
		uni.getLocation({
			type: 'gcj02',
			success: function(res) {
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
				this.latitude = res.latitude;
				this.longitude = res.longitude;
			},
			complete: function(res) {
				that.inquiryNearbyMerchant(res.latitude, res.longitude);
			}
		});
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		selectMerchant(item){
			this.$api.prePage().merchantData = item;
			uni.navigateBack()
		},
		openLocation(item){
			debugger;
			uni.openLocation({
				latitude: item.latitude,
				longitude: item.longitude,
				success: function () {
					console.log('打开地图成功');
				}
			});
		},
		findCover(val) {
			var covers = [];
			covers.push(this.getCover(val));
			this.covers = covers;
		},
		//根据一个门店产生标记点
		getCover(val) {
			return {
				latitude: val.latitude,
				longitude: val.longitude,
				width: 40,
				height: 40,
				iconPath: '../../static/image/address.png',
				callout: {
					content: val.merchantName,
					borderRadius: "10",
					bgColor: "#F44F31",
					color: '#FFFFFF',
					padding: 5,
					display: 'ALWAYS',
					textAlign: 'center'
				}
			}
		},
		//查询附近门店
		inquiryNearbyMerchant(lat, lng) {
			let that = this;
			this.$api.request.nearbyMerchant({
				userLatitude: lat||this.latitude,
				userLongitude: lng||this.longitude
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.merchantList = res.body.data.merchants;
					var covers = [];
					this.merchantList.forEach(function(val, index) {
						covers.push(that.getCover(val));
					})
					this.covers = covers;
				} else {
					this.$api.msg(res.body.status.errorDesc);
				}
			});
		}
	}
}
